import React from 'react';
import styled from 'styled-components';
// import Carousel, { arrowsPlugin } from '@brainhubeu/react-carousel';
import '@brainhubeu/react-carousel/lib/style.css';

import her1 from '../img/hermanas11.jfif';
import her2 from '../img/hermanas22.jfif';
import her3 from '../img/hermanas33.jfif';
import collage from '../img/collage.png';


// import Slider from 'react-animated-slider';
import 'react-animated-slider/build/horizontal.css';
import Slider from "react-slick";

const Imagen = styled.div`
    width: 100%;
    height: 80vh;
    padding-top: 0px;
    background-image: url(${collage});
    background-size: contain;
    background-position: center;
    background-repeat: no-repeat;   
`

const Imagen2 = styled.div`
    width: 90%;
    height: 80vh;
    padding-top: 70px;
    background-image: url(${her1});
    background-size: cover;
    background-position: center ;
    background-repeat: no-repeat;  
`

const Imagen3 = styled.div`
    width: 100%;
    height: 80vh;
    padding-top: 70px;
    background-image: url(${her2});
    background-size: cover;
    background-position: center ;
    background-repeat: no-repeat;  
`

const Imagen4 = styled.div`
    width: 100%;
    height: 80vh;
    padding-top: 70px;
    background-image: url(${her3});
    /* background-size: cover; */
    background-position: center;
    background-repeat: no-repeat;   
`

const Titulo = styled.h1`
    position: absolute;
    z-index: 1;
    top: 45%;
    color: white;
    font-size: 25px;
    text-align: left;
    margin-left: 20px;
    margin-top: 140px;
    width: auto;
    left: 0;
    color: #9DD1FF;
    filter: drop-shadow(3px 2px 2px rgba(0, 0, 0, 1));
    /* background: red; */
    @media (min-width: 768px) {
        font-size: 58px;
        font-weight: 900;
        margin-left: 100px;
    }
`

export default function Carousel() {
  var settings = {
    dots: true,
    infinite: true,
    autoplay: true,
    speed: 1000,
    fade: true,
    slidesToShow: 1,
    slidesToScroll: 1,
  };
  return (
      <div className="container2">
        <Titulo className="tituloCarousel">HERMANAS <br /> MISIONERAS DE  <br />SAN FRANCISCO JAVIER</Titulo>
        <Slider {...settings}>
            <Imagen  />
            <Imagen3  />
            <Imagen2  />
            <Imagen4 />
        </Slider>
      </div>
  );
}