import React from 'react';
import styled from 'styled-components';

import enseñanza from '../img/enseñanza.png';
import carisma from '../img/carisma.png';
import visita from '../img/visita.png';
import misioneras from '../img/misioneras.png';

const Container = styled.div`
    display: flex; 
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    width: 95%;
    margin-bottom: 100px;
    
    @media (min-width: 768px) {
        width: 60%;
        min-height: 300px;
    }
    /* background: red; */
`

const ContainerActividad = styled.div`
    width: 100%;
    max-width: 300px;
    min-height: 300px;
    margin: 20px 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 20px;
    filter: drop-shadow(5px 5px 8px rgba(0, 0, 0, .4));
    overflow: hidden;
    &:hover div{
        background: rgba(0, 0, 0, 0.6);
        transition: all .5s;
    }
`

const TituloActividad = styled.h2`
    width: auto;
    text-align: center;
    position: absolute;
    padding: 10px;
    border-radius: 10px;
    color: white;
`

const Link = styled.a`
    text-decoration: none;
    color: #54F777;
    padding: 5px 8px;
    font-size: 18px;
    margin-top: 17px;
    border-radius: 15px;
    border: solid 3px #54F777;
    transition: all .2s;
    z-index: 100;
    &:hover {
        background: #54F777;
        color: white;
        transition: all .2s;
    }
`

const OscurecerFoto = styled.div`
    background: transparent;
    width: 100%;
    height: 100%;
    transition: all .5s;
`

const act = [
    {
        link: "/iglesiacarmen/carisma-y-espiritualidad/#ensenanza",
        foto: enseñanza,
        titulo: "Enseñanza"
    },
    {
        link: "/iglesiacarmen/carisma-y-espiritualidad/#necesitados",
        foto: carisma,
        titulo: "Ayuda a los más necesitados"
    },
    {
        link: "/iglesiacarmen/carisma-y-espiritualidad/#visita",
        foto: visita,
        titulo: "Visita a enfermos"
    },
    {
        link: "/iglesiacarmen/carisma-y-espiritualidad/#misioneras",
        foto: misioneras,
        titulo: "Actividad misionera"
    },
    
]

const Actividades = () => {

    const ListarActividades =  act.map((actividad, index) => {
        return (
            <ContainerActividad className="containerAct imgAct" 
            key={index}
            style={{backgroundImage: `url(${actividad.foto})`,
            backgroundPosition: "center center",
            backgroundSize: "cover",
            width: 300,
            height: 300,
            }}
            >
                {/* <Img src={actividad.foto} className="imgAct" /> */}
                <TituloActividad className="tituloAct">{actividad.titulo} <br /> <br />
                    <Link href={actividad.link} className="linkActividad" >Ver más</Link>
                </TituloActividad>
                <OscurecerFoto />
            </ContainerActividad>
        );
    })

    return ( 
        <Container>
            {ListarActividades}
        </Container>

    );
}
 
export default Actividades;