import React from 'react';
import styled from 'styled-components';
import Actividades from '../components/Actividades';

import Carousel from '../components/Carousel';
import ComponentHome from '../components/ComponentHome'; 
import {Container, Titulo, SubTitulo} from '../components/GlobalStyles';

import {Link} from 'react-router-dom';


const ContainerNovedades = styled.div`
    width: 100%;
    background: white;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;

`

const Home = () => {
    return (  
        <Container data-aos="fade-down" >
            {/* <Toolbar /> */}
            <Carousel />
            <Titulo>¿Quiénes somos?</Titulo>
            <SubTitulo style={{textAlign: "center", marginBottom: "100px", marginTop: 0}}>Somos un Instituto religioso fundado en Argentina, en la localidad de Vila Raffo, partido de Tres de Febrero, diócesis de San Martin, provincia de Buenos Aires por la Sierva de Dios Madre Isabel Fernández del Carmen que nace para dar testimonio de Cristo en las misiones tanto rurales como en las grandes ciudades; en la atención a los más necesitados y a los enfermos. Como guía tenemos a san Francisco Javier y a santa Teresita del Niño Jesús y nuestra señora del Carmen, es la estrella que nos lleva y empuja a ir más allá de las fronteras, aún en las ciudades; donde el nombre de Jesús, está desapareciendo por el ruido y la banalidad.</SubTitulo>
            <ContainerNovedades>
                <Titulo style={{marginTop: "20px"}}><Link to="/iglesiacarmen/novedades" style={{textDecoration: "none", color:"#2e2e2e"}}>NOVEDADES DE LAS HERMANAS MISIONERAS</Link></Titulo>
            </ContainerNovedades>
            <ComponentHome />
            <Titulo>Nuestras Actividades</Titulo>
            <Actividades />

        </Container>
    );
}
 
export default Home;