import React, {useState, useEffect} from 'react';
import styled from 'styled-components';

import musica from '../music/musica.mp3'
import {BsFillPlayFill} from 'react-icons/bs';
import {BsFillStopFill} from 'react-icons/bs';

const Container = styled.div`
    width: auto; 
    height: auto;
    display: flex;
    align-items: center;
    background: white;
    border-radius: 20px;
    padding: 5px 10px;
    position: fixed;
    right: 20px;
    bottom: 20px;
    z-index: 200;
    font-weight: 600;
    filter: drop-shadow(0px 5px 5px rgba(0, 0, 0, .2));
    @media(max-width: 768px) {
        display: none;
    }
`

const StopPlay = styled.button`
    all: unset;
    padding-top: 5px;
`

const useAudio = url => {
    const [audio] = useState(new Audio(musica));
    const [playing, setPlaying] = useState(false);
  
    const toggle = () => setPlaying(!playing);
  
    useEffect(() => {
        playing ? audio.play() : audio.pause();
      },
      [playing]
    );
  
    useEffect(() => {
      audio.addEventListener('ended', () => setPlaying(false));
      return () => {
        audio.removeEventListener('ended', () => setPlaying(false));
      };
    }, []);
  
    return [playing, toggle];
  };
  
  const MusicaFondo = ({ url }) => {
    const [playing, toggle] = useAudio(url);
  
    return (
      <Container>
          Escuchar música armoniosa
        <StopPlay onClick={toggle}>{playing ? <BsFillStopFill style={{fontSize: 30, cursor: "pointer"}} /> : <BsFillPlayFill style={{fontSize: 30, cursor: "pointer"}} />}</StopPlay>
      </Container>
    );
  };
 
export default MusicaFondo;