import React from 'react';
import styled from 'styled-components';
import {Container, Titulo,} from '../components/GlobalStyles';

import AOS from 'aos';
import 'aos/dist/aos.css';

AOS.init();

const ContainerUbicaciones = styled.div`
    display: flex;
    justify-content: space-evenly;
    flex-wrap: wrap;
    width: 70%;
    min-height: 300px;
    /* background: red; */
    @media (max-width: 768px) {
        width: 90%;
    }
`

const Ubicacion = styled.div`
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    width: 400px; 
    min-height: 200px;
    background: white;
    filter: drop-shadow(5px 5px 8px rgba(0, 0, 0, .2));
    border-radius: 20px;
    margin-bottom: 20px;
    transition: all .4s;

    @media (max-width: 768px) {
        width: 70%;
    }

    &:hover {
        transform: scale(1.05);
        transition: all .4s;
    }
`

const Info = styled.p`
    margin-left: 20px;
    color: #2e2e2e;
    font-size: 16px;
    font-weight: 900;
    width: 100%;
    text-align: left;
`
const Link = styled.a`
    text-decoration: none;
    color: #2e2e2e;
`

