const Contacto = () => {

    const listarContactos = contactos.map((contacto, index) => {
        return (
            <Ubicacion key={index}>
            <Titulo>{contacto.lugar}</Titulo>
            <Info>
                <Link href={contacto.direccionLink} target="_blank"> 
                    {contacto.direccion} <br/> {contacto.direccion2}
                </Link>
            </Info>
            <Info>
                <Link href={contacto.telefonoLink} target="_blank">
                    {contacto.telefono}
                </Link> 
            </Info>
            <Info>
                <Link href={contacto.emailLink} target="_blank">
                    {contacto.email}
                </Link> 
            </Info>
        </Ubicacion>
        )
    })

    return (  
        <Container data-aos="fade-down">
            <Titulo style={{marginTop: "120px"}}>Contáctanos</Titulo>
            <ContainerUbicaciones>
                {listarContactos}
            </ContainerUbicaciones>
        </Container>
    );
}
 
export default Contacto;