import React from 'react';
import styled from 'styled-components';

import {Container, Titulo, Texto} from '../components/GlobalStyles';

import AOS from 'aos';
import 'aos/dist/aos.css';

import novedad1 from '../img/novedad1.jpg';
import novedad2 from '../img/novedad2.jpg';
import novedad3 from '../img/novedad3.jpg';
import novedad4 from '../img/novedad4.jpg';

AOS.init();

const ContainerNovedades = styled.div`
    width: 80%;
    min-height: 200px;
`

const ContainerNovedad = styled.div`
    display: flex;
    align-items: center;
    margin: 20px;
    width: 90%;
    max-width: 900px;
    height: auto;
    border-bottom: 2px solid #2e2e2e;
    padding: 30px 0;
    @media(max-width: 768px) {
        flex-direction: column;
        align-items: center;
    }
`

const Img = styled.div`
    margin-right: 50px;
    @media(max-width: 768px) {
        margin-right: 0;
        margin-bottom: 20px;
    }
`

const novedades = [
    {
        foto: novedad1,
        texto: "Ha ingresado al postulantado del nuestro instituto Cecilia Luján Eder; quiera Dios que culmine en ella la labor que ha iniciado y sea, dentro de poco una hermana más de nuestro instituto para la gloria de Dios por medio de las misiones y la ayuda a los demás. bienvenida Cecilia!"
    },
    {
        foto: novedad2,
        texto: "Se está en la ultima fase de estudio de la posible curación milagrosa obtenida  por la intercesión de la Sierva de Dios Madre Isabel Fernández. De ser positivo el fallo de los médicos, se iniciará el Proceso 'Super Miro' (sobre el milagro) por lo cual la Madre Isabel, estaría pronta a su Beatificación"
    },
    {
        foto: novedad3,
        texto: "Con motivo de dar a conocer la figura de la Madre Isabel, se ha decidido realizar copias de la imagen de la Sierva de dios, para poder recorrer con ella varias comunidades y así, dar a conocer la vida y obra de la Sierva de Dios de la Diócesis de San Martin."
    },
    {
        foto: novedad4,
        texto: "Todo el mes de Marzo, el cual está dedicado a san José, la imagen restaurada del santo se expondrá junto al altar  en este año jubilar dedicado al Santo. San José, protege a nuestro instituto y bendice a toda la Iglesia."
    },
]

const listarNovedades = novedades.map((novedad, index) => {
    return (
        <ContainerNovedad key={index}>
            <Img
            style={{
            backgroundImage: `url(${novedad.foto})`,
            backgroundPosition: "center center",
            backgroundSize: "cover",
            width: "100%",
            height: 400,
            }}
            />
            <Texto style={{textIndent: 0, fontSize: 25}}>{novedad.texto}</Texto>

        </ContainerNovedad>
    )
})

const Novedades = () => {

    return ( 
        <Container data-aos="fade-down">
            <Titulo style={{marginTop: 120}}>Novedades</Titulo> 
            <ContainerNovedades>
                {listarNovedades}
            </ContainerNovedades>  
        </Container>        
    );
}
 
export default Novedades;